:root {
  --primary: #9C27B0;
  --primary-dark: #7B1FA2;
  --primary-light: #E1BEE7;
  --primary-text: #212121;
  --secondary-text: #757575;
  --text-light: #ffffff;
  --background-light: #ddd;
  --accent: #7C4DFF;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: var(--background-light);
}

.container {
  width: 70%;
  margin-left: auto;
  display: block;
  height: 100%;
}

.section {
  width: 100%;
  height: 100%;
  clear: both;
}

[class*="col-"] {
float: left;
}

@media only screen and (min-width: 768px) {
  .col-1 {width: 8.33%;}
  .col-2 {width: 16.66%;}
  .col-3 {width: 25%;}
  .col-4 {width: 33.33%;}
  .col-5 {width: 41.66%;}
  .col-6 {width: 50%;}
  .col-7 {width: 58.33%;}
  .col-8 {width: 66.66%;}
  .col-9 {width: 75%;}
  .col-10 {width: 83.33%;}
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}

.header {
  background: var(--primary-dark);
}

.header-container {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  color: var(--text-light);
  font-weight: bold;
  font-size: 20px;
  display: flex;
  align-items: center;
}
.header-logo div {
  margin-left: 10px;
}

.header-country button {
  background: var(--primary-light);
  color: var(--primary-text);
  font-weight: bold;
}

.header-country button:hover, .header-country button:focus {
  background: var(--primary-dark);
  color: var(--text-light);
}

.progress {
  margin: 0 !important;
}

.global-numbers {
  width: 70%;
  margin-left: 15%;
  display: flex;
  justify-content: space-between;
}

.card-header {
  width: 100%;
  background: #ccc;
  color: var(--primary-text);
  font-weight: bold;
  padding: 10px;
  text-align: center;
}

.card-panel {
  padding: 0 !important;
}

.cases {
  text-align: center !important;
  font-weight: bold;
  padding: 20px;
  font-size: larger;
}

select {
  display: table;
}

.countryImage {
  position: relative;
  transition: all 1s;
  overflow: hidden;
}

.countryImage>img {
  margin: 0 !important;
  
  transition: all 0.5s;
}
.countryImage:hover>img {
  opacity: 0.5;
  transform: scale(1.2);
}
.countryImage>div.opaque-div {
  display: none;
  position: absolute;
  bottom: 0px;
  left: 0;
  opacity: 0.7;
  width: 100%;
  background: #000;
  color: #000;
  padding: 20px;
  text-align: center;
  transition: all 5s;

}
.countryImage:hover> .opaque-div {
  display: block;
  transition: all 1s ease-in;
}
.opaque-text {
  font-weight: bolder;
  font-size: 20px;
  color: #fff;
  position: relative;
  z-index: 200;
  transition: all 1s;
}


